h2, p {
  text-align:center;
  font-family:sans-serif;
  margin:30px 0;
}
section {
/*   max-width:900px; */
  margin:0 auto;
}

.responsive-iframe-container {
  margin:0 auto;
    position: relative;
    height:0;
    overflow: hidden;
}
.responsive-iframe-container iframe
/*.vresponsive-iframe-container object,*/
/*.vresponsive-iframe-container embed */ 
{
    position: absolute;
    top:0;
    left:0;
    width: 100%;
    height: 100%;
}
@media (max-width:549px) {
  .desktop-header {
    display:none;
  }
    .large-container {
        display: none;
    }
    .responsive-iframe-container {
        padding-bottom:500px;
    width:100%;
    }
}
@media (min-width:550px) {
  .mobile-header {
    display:none;
  }
    .small-container {
        display: none;
    }
    .responsive-iframe-container {
        padding-bottom:650px;
/*     width:800px; */
    width: 100%;
    max-width: 1400px;
    }
}